<template>
  <div class="page-setting">
    <section class="setting-card">
      <ul class="settings clearfix">
        <li class="setting-item" v-for="(setting, index) in settings" :key="index">
          {{setting.text}}
          <span class="explain" v-if="index===0"  @click="showModal('提醒规则')">
            提醒规则
            <i><icon name="question-circle-o"></icon></i>
          </span>
          <span :class="setting.status ? 'check-icon' :  'uncheck-icon'" @click="toggleCheck(index)">
            <icon :name="setting.status ? 'toggle-on' : 'toggle-off'" scale="2.5"></icon>
          </span>
        </li>
      </ul>
    </section>
    <section class="popups">
      <ul>
        <li class="popup" v-for="(item, index) in popData" :key="index"
          @click="showModal(item.text)">
          <img :src="item.img" alt="">
          {{item.text}}
        </li>
      </ul>
    </section>
    <Footer />
    <Modal :title="popTitle" :showPop="showPop" :close="closeModal" :opaque="popTitle !== '提醒规则'" :autoHeight="popTitle === '提醒规则'">
      <div>
        <TermOfUse v-if="popTitle === '优行™使用条款'" />
        <div class="intro" v-if="popTitle === '优行™项目介绍'">
          <p class="img-wrp"><img src="../../../assets/Setting/brand_logo.png" alt=""></p>
          <p>
            借助智能的血糖仪设备、便携的微信平台、专业的关爱中心支持帮助医生高效管理患者，
            提升诊疗效率。帮助患者养成自我监测血糖的习惯，提高患者的依从性。
          </p>
          <p class="img-wrp"><img src="../../../assets/Setting/introduce.png" alt=""></p>
          <div>
            为礼来优泌乐系列胰岛素用户 <br>
            打造糖尿病管理综合解决方案
          </div>
        </div>
        <div class="msg-rule" v-if="popTitle === '提醒规则'">
          <p>
            当患者血糖符合以下条件时，会触发微信模板消息提醒：
          </p>
          <ol>
            <li>
              1. 患者出现极低血糖(≤3.0mmol/L)或出<br><span>现高血糖(≥16.7mmol/L)时</span>
            </li>
            <li>
              2. 患者出现连续3天低血糖(≤3.9 mmol/<br><span>L)时</span>
            </li>
            <li>
              3. 患者周测试频率≤5次，50%测量点为<br><span>低血糖(≤3.9mmol/L)时</span>
            </li>
          </ol>
        </div>
      </div>
    </Modal>
  </div>
</template>
<script>
import Footer from '../../Shared/Footer/Footer'
import Modal from '../../Shared/Modal/Modal'
import TermOfUse from './TermOfUse'
import popImg1 from '../../../assets/Setting/popImg1.png'
import popImg2 from '../../../assets/Setting/popImg2.png'
import popImg3 from '../../../assets/Setting/popImg3.png'
import popImg4 from '../../../assets/Setting/popImg4.png'
import { mapGetters } from 'vuex'
export default {
  name: 'Setting',
  components: {
    Footer,
    Modal,
    TermOfUse
  },
  data () {
    return {
      popData: [
        {
          text: '医生端介绍',
          img: popImg1
        },
        {
          text: '患者端介绍',
          img: popImg2
        },
        {
          text: '优行™项目介绍',
          img: popImg3
        },
        {
          text: '优行™使用条款',
          img: popImg4
        }
      ],
      showPop: false,
      popTitle: ''
    }
  },
  computed: {
    ...mapGetters({
      username: 'username',
      settingStatus: 'settingStatus',
      settings: 'settings'
    })
  },
  methods: {
    showModal (title) {
      this.$store.dispatch('pageClickTracking', {logType: 'click', message: title})
      this.showPop = true
      this.popTitle = title
    },
    closeModal: function () {
      this.showPop = false
    },
    toggleCheck: function (index) {
      let settings = this.settings
      settings[index].status = settings[index].status === 'on' ? '' : 'on'
      let result = {}
      this.settings.forEach(item => {
        result[item.map] = item.status
      })
      let data = {
        result: result,
        settings: settings
      }
      this.$store.dispatch('setPushSetting', data)
    }
  },
  beforeMount () {
    if (!this.settingStatus.length) {
      this.$store.dispatch('fetchSettingList')
      this.settingData = this.settings
    }
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



